import styles from "./OrderDetails.module.css";
import image from "../../images/done.png";
import React from "react";
import {useSelector} from "react-redux";


const OrderDetails = () => {
    const {order} = useSelector(store => store.popupOrderReducer);
    return (<div className={styles.popup_container}>
            <h2 className={`${styles.popup_heading} text text_type_digits-large`}>{order}</h2>
            <p className='text text_type_main-medium'> </p>
            <img src={image} className={styles.popup_done} alt={''}/>
            <p className={`${styles.popup_paragraph} text text_type_main-default`}>   </p>
            <p className={`${styles.popup_paragraph} 'text text_type_main-default text_color_inactive`}>
                   </p>
        </div>)
}

export default OrderDetails;